* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


/* grid content style */
h2 {
  font-size: 30px;
  font-weight: 300;
  margin: 10px;
}

.homeName {
  font-size: 40px;
  font-weight: 600;
}

.res {
  text-decoration: none;
  font-size: 30px;
  color: white;
}

p {
  font-size: 20px;
}

/* home logo scroll to top*/
.homeb {
  position: fixed;
  z-index: 1;
  color: #fff;
  top: 5px;
  right: 5px;
  padding: 20px 10px;
  /* margin-left: 100rem; */
  /* padding-top: 1rem; */
  /* display: flex; */
  /* align-items:flex-end;
  justify-content: right;
  text-align: right; */
}

.homeb:hover {
  transform: scale(0.9);
  transition: all 0.5s ease-in;
}

/* about general setting */
.section-head {
  font-size: 2rem;
  margin-top: 10vh;
}

.section-content {
  font-size: 1.3rem;
  padding-left: 16rem;
  padding-right: 16rem;
}

/* about me section */
.aboutMe {
  background: #1e1e1e;
  color: white;
  overflow-y: hidden;
  padding: 2rem 0;
  line-height: 1.6;
  text-align: center;
  margin: 0 auto;
  /* height: 100vh; */
  width: 100%;
}

.aboutbg {
  padding-top:  2rem;
  width: 80%;
}

/* what I do section */
.ido {
  /* background: lightgrey; */
  background: linear-gradient(to right, rgba(33,33,33,0.8), rgba(255,255,255,0));
  color: black;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  overflow-y: hidden;
  padding: 10rem 0;
  line-height: 1.6;
  text-align: left;
  width: 100%;
  /* text-indent: 2rem; */
}

.running {
  font-size: 10rem;
  text-align: center;
}

.ido > div:nth-of-type(2) {
  grid-column: 2 / 4;
}

