/* education */
.education {
  padding: 0 1rem;
  list-style: none;
  line-height: 1.6em;
  font-size: 1rem;
  text-align: left;
}

/* expertise */
.skills {
  display:grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 50px;
  text-align: center;
}

.languages {
  list-style: none;
  line-height: 1.6em;
  font-size: 1rem;
  text-align: center;
}

.frameworks {
  list-style: none;
  line-height: 1.6em;
  font-size: 1rem;
  text-align: center;
}

/* container grid setting */
.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  background: #282828;
  color: #fff;
  font-family: 'Lato', sans-serif;
}

.container > div, .front > div, .link > div{
  cursor: pointer;
  height: 210px;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  width: 100%;
  /* height: 25vh; */
  position: relative;
  overflow-y: hidden;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.5s ease-in;
}

.back {
  height: 210px;
  cursor: pointer;
  display: flex;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.5s ease-in;
}

.container > h2 {
  font-size: 45px;
  font-weight: 300;
  margin: 10px;
}

.container > h2 span {
  font-size: 30px;
}

.container > p {
  font-size: 20px;
}

.container > div:hover {
  opacity: 0.7;
  transform: scale(0.98);
}

.link > div:hover {
  opacity: 0.7;
  transform: scale(0.98);
}

.container > div:nth-of-type(1) {
  grid-column: 1 / 5;
}

.container > div:nth-of-type(4) {
  grid-column: 1 / 3;
}

.container > div:nth-of-type(8) {
  grid-column: 2 / 5;
}

.bg1 {
  background: url(/static/media/hk.a1e59733.jpg);
}

.bg2 {
  background: url(/static/media/hk.a1e59733.jpg);
  color: white;
}

