@media screen and (max-width: 580px) {
.in-small {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 4px;
}

.homeName {
    font-size: 25px;
    font-weight: 600;
}

.container > div:nth-of-type(1) {
  grid-column: auto;
}

.container > div:nth-of-type(4) {
  grid-column: auto;
}

.container > div:nth-of-type(8) {
  grid-column: auto;
}

